    box-shadow:0 2px 3px #ccc;
    border:1px solid #eee;
    padding:10px;
    box-sizing: border-box;
    background-color:  #8b0000	(82, 181, 234, 0.2);
    color: #eee;
}
.ContactData input{
    display: block ;
}
